@media screen and (min-width: 415px){
  .site-container {
    flex-direction: column;
    flex: 1;
  }
}

.detail-view {
    width: 37%;
    height: 100%;
    padding: 10px;
    background-color: #ff00008a;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .sprite-image {
    width: 80%;
    height: 150px;
    margin: 10px auto;
    background-color: #ffffff;
    border: 15px solid #0e0000;
    border-radius: 5px;
    color: #585858;
  }
  
  .data-wrapper {
    width: 90%;
    height: 110px;
    padding: 10px;
    margin: 0 auto;
    background-color: #000000;
    border-radius: 5px;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
    color: #FFF;
  }
  
  .data-name {
    margin: 0 0 5px;
  }
  
  .data-char {
    font-size: 15px;
  }