.poke-cell {
    width: 127px;
    height: 130px;
    margin: 10px;
    background-color: #FFF;
    background-repeat: no-repeat;
    border: block;
    border-radius: 10px;
    box-shadow: 0 5px 2px 0 rgba(0, 0, 0, 0.16);
  }
@media screen and (min-width: 415px){
